* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.app {
  width: 100%;
  height: 100vh;
  background: rgb(0, 172, 100);
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  position: relative;
  overflow-y: scroll;
}

.app::-webkit-scrollbar { 
  width: 0; 
}
.app { 
  -ms-overflow-style: none; 
}
.app { 
  overflow: -moz-scrollbars-none; 
}

.video__container {
  width: 45%;
  margin: 10px;
}

.video {
  width: 100%;
  height: 100%;
}

.fullvid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  flex-direction: column;
  overflow: hidden;
  align-items: center;
}

.full-video {
  height: 100%;
  position: relative;
}

.closevid {
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin: 10px;
  position: absolute;
  top: 0;
  right: 0;
  background: rgba(32, 31, 31, 0.9) url('img/close.svg') no-repeat center;
  z-index: 5;
}

.controls {
  width: 100%;
  background: black;
  position: absolute;
  bottom: 0;
  display: flex;
  transform: translateY(60px);
  transition: 0.3s;
  transition-delay: 3s;
  &:hover {
    transform: translateY(0);
    transition: 0.3s;
  }
}

.progressbar {
  height: 15px;
  width: 100%;
  position: absolute;
  top: -15px;
  background: rgba(32, 31, 31, 0.9);
}

.progress {
  background: red;
  height: 100%;
}

#play-pause {
  background: url('img/play.svg') no-repeat center;
  background-size: cover;
  width: 40px;
  height: 40px;
  margin: 10px;
  &.play {
    background: url('img/pause.svg') no-repeat center;
    background-size: cover;
  }
  &.pause {
    background: url('img/play.svg') no-repeat center;
    background-size: cover;
  }
}

.volumectrl {
  width: 150px;
}

.preloader {
  position: absolute;
  width: 100%;
  height: 100vh;
  background: rgb(0, 172, 100);
  display: flex;
  justify-content: center;
  align-items: center;
}

.bar-wrap {
  width: 75px;
  height: 150px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.bar {
  width: 15px;
  height: 80px;
  background: #fff;
  animation: preload .5s infinite;
}

.reverse {
  animation: preload .5s infinite reverse;
}

.hide {
  transform: scaleY(0);
  transition: 0.5s;
}

@keyframes preload {
  0% {
    transform: scaleY(.5);
  }
  50% {
    transform: scaleY(1);
  }
  100% {
    transform: scaleY(.5);
  }
}